.user-control__item {
  display: flex;
  align-items: center;

  &:not(:last-child) {
    margin-right: 35px;
  }
}

.user-control__item p {
  margin: 0;
}

.user-control__link {
  @include font(16px, 19px, 400, #515151);

  padding: 15px 0;

  &--subscribe {
    color: #1d1d1d;
    border: 1px solid #1d1d1d;
    text-transform: uppercase;
    padding: 10px 20px;
  }

  @include vp-767 {
    &--subscribe {
      display: none;
    }
  }
}
